// Use this file to overwrite the basic Bootstrap variables and add your own variables
// To overwrite a Bootstrap variable you don´t have to touch the Bootstrap folder.
// Just copy a variable from src/sass/bootstrap4/_variables.scss, paste it here and edit the value.

// Color

$primary: #0d46ff;
$secondary: #0066b1;
$accent: #000000;

$grey-1: #f2f2f2;
$grey-2: #f6f8f9;
$grey-3: #f6f8f9; //
$grey-4: #6f6f6f;
$grey-5: #707070;
$grey-6: #cccccc;
$grey-7: #505050;

//Font
$global-font: "Montserrat", sans-serif;

//Font Weight
$regular: 400;
$medium: 500;
$semi-bold: 600;
$bold: 700;
$extra-bold: 800;

$grid-breakpoints: (
	xs: 0,
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px,
	xxl: 1400px,
) !default;

// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

$container-max-widths: (
	sm: 540px,
	md: 720px,
	lg: 960px,
	xl: 1140px,
	xxl: 1270px,
) !default;
